.wrapper {
    height: 100vh;
    display: grid;
    place-items: center;
}

.box {
    padding: 0 5%;
    height: 300px;
    background-color: lightblue;
    border-radius: 40px;
}

.box form {
    margin-top: 30px;
}

.text {
    top: 0px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: white;
    font-weight: 1000;
    font-size: 60px;
    margin: 0;
    height: 60px;
}

.input-box {
    display: flex;
    place-items: center;
}

.input-box input {
    background-color: rgb(125, 182, 201);
    margin: 5px 0;
    width: 100%;
    height: 30px;
    border-radius: 40px;
}

.btn {
    background-color: rgb(125, 182, 201);
    width: 100%;
    height: 30px;
    border-radius: 40px;
    color: white;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: 500;
    font-size: 20px;
}